/*--------------------------------------------------------------
1. Basic Color Variable
----------------------------------------------------------------*/
@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
:root {
  --body-font-family: "DM Sans", sans-serif;
  --heading-font-family: "Lato", sans-serif;
  --body-color: #003168;
  --body-bg-color: #fdfbf7;
  --body-bg-color-two: #f4ffff;
  --heading-color: #003168;
  --border-color: #e4e3e1;
  --white-color: #ffffff;
  --black-color: #021d5e;
  --primary-color: #efbf04;
}

/*--------------------------------------------------------------
2. Typography
----------------------------------------------------------------*/
html,
body {
  color: var(--body-color);
  font-family: var(--body-font-family);
  background-color: var(--body-bg-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6em;
  overflow-x: hidden;
  scroll-behavior: auto;
  padding: 0 !important;
}

body.body-bg-color-two {
  background-color: var(--body-bg-color-two);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  color: var(--heading-color);
  font-family: var(--heading-font-family);
  padding: 0px;
  margin: 0px;
  font-weight: 400;
  line-height: normal;
  font-style: normal;
}

h1 {
  font-size: 62px;
}

h2 {
  font-size: 50px;
}

h3 {
  font-size: 36px;
}

h4 {
  font-size: 28px;
}

h5 {
  font-size: 24px;
}

h6 {
  font-size: 20px;
}

p {
  margin-bottom: 0;
}

ul {
  margin: 0 0 25px 0;
  padding-left: 20px;
  list-style: square outside none;
}

ol {
  padding-left: 20px;
  margin-bottom: 25px;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 15px;
  font-style: italic;
  font-size: 20px;
  line-height: 1.6em;
  margin: 0;
}

address {
  margin: 0 0 15px;
}

img {
  border: 0;
  max-width: 100%;
  height: auto;
}

.logo{
  height: 141px;
}

a {
  color:  #003168;
  text-decoration: none;
  transition: all 0.3s ease;
}

button {
  color: inherit;
  transition: all 0.3s ease;
}

a:hover {
  text-decoration: none;
  color:  #003168;
  transition: all 0.3s ease;
}

table {
  width: 100%;
  margin-bottom: 25px;
}
table th {
  font-weight: 600;
  color: var(--body-color);
}
table td,
table th {
  border-top: 1px solid #fff;
  padding: 11px 10px;
}

dl {
  margin-bottom: 25px;
}
dl dt {
  font-weight: 600;
}

b,
strong {
  font-weight: bold;
}

pre {
  color: var(--body-color);
  border: 1px solid #fff;
  font-size: 18px;
  padding: 25px;
  border-radius: 5px;
}

kbd {
  font-size: 100%;
  background-color: var(--body-color);
  border-radius: 5px;
}

/*--------------------------------------------------------------
3. Preloader
----------------------------------------------------------------*/
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #fff;
  z-index: 99999999;
  overflow: hidden;
  transform: translateY(0);
}

.txt-loading {
  color:  #003168;
  font-family: var(--body-font-family);
  font-size: 60px;
  font-weight: 700;
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.txt-loading .preloader-text {
  text-align: center;
}
.txt-loading .preloader-text .logo-icon {
  width: 80px;
}
.txt-loading .loading-percent {
  max-width: 200px;
  min-width: 150px;
}
.txt-loading .loading-border-content {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  padding: 0;
  gap: 10vw;
  align-items: flex-end;
  bottom: 91px;
  left: 50px;
}

.loading-border-show {
  background-color: var(--border-color);
  height: 2px;
  width: 100%;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 8px;
}
.loading-border-show .loading-border {
  background-color: var(--body-color);
  height: 2px;
  width: 0;
  border-radius: 4px;
}

@media screen and (max-width: 991px) {
  .txt-loading {
    font-size: 42px;
  }
  .txt-loading .loading-border-content {
    left: 15px;
  }
  .txt-loading .loading-border-content .loading-percent {
    min-width: 115px;
  }
}
