.common-btn {
  background-color: var(--primary-color);
  font-family: var(--body-font-family);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s;
  padding: 12px 28px;
  border-radius: 0px;
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 11px;
  overflow: hidden;
  width: auto;
}
.common-btn span {
  color: var(--black-color);
  position: relative;
  z-index: 111;
  transition: all 0.5s;
}
.common-btn::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 100%;
  visibility: hidden;
  overflow: hidden;
  background-color: var(--black-color);
  transition: all 0.3s;
  z-index: 11;
}
.common-btn:hover span {
  color: var(--white-color);
}
.common-btn:hover span path {
  stroke: var(--white-color);
}
.common-btn:hover::before {
  right: 0;
  visibility: visible;
}
.common-btn.style-2 {
  background-color: var(--black-color);
}
.common-btn.style-2::before {
  background-color: var(--primary-color);
}
.common-btn.style-2 span {
  color: var(--white-color);
}
.common-btn.style-2 .arrow-cricle {
  position: relative;
  display: flex;
  background-color: var(--white-color);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  z-index: 111;
  overflow: hidden;
}
.common-btn.style-2 .arrow-cricle > :first-child {
  transition: all 0.15s;
  transform: translateX(-150%);
}
.common-btn.style-2 .arrow-cricle > :last-child {
  transition: all 0.2s;
  transform: translateX(-50%);
}
.common-btn.style-2:hover span {
  color: #000;
}
.common-btn.style-2:hover .arrow-cricle path {
  stroke: var(--black-color);
}
.common-btn.style-2:hover .arrow-cricle > :first-child {
  transform: translateX(50%);
}
.common-btn.style-2:hover .arrow-cricle > :last-child {
  transform: translateX(170%) rotateX(50deg);
}
.common-btn.style-2.color-2 {
  background: rgba(255, 255, 255, 0.1);
}
.common-btn.style-2.type-2 {
  background: transparent;
  border: none;
  border-radius: 50%;
  padding: 5px 5px;
}
.common-btn.style-2.color-3 {
  background-color: #9fe970;
}
.common-btn.style-2.color-3::before {
  background-color: var(--black-color);
}
.common-btn.style-2.color-3 span {
  color: var(--black-color);
}
.common-btn.style-2.color-3:hover span {
  color: var(--white-color);
}
.common-btn.style-3 {
  background-color: transparent;
  border: 1px solid rgba(3, 9, 23, 0.1);
}

.more-btn {
  font-family: var(--body-font-family);
  position: relative;
  display: inline-flex;
  color:  #003168;
  font-size: 16px;
  font-weight: 500;
  min-width: 100px;
