  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 111;
  overflow: hidden;
}
.more-btn .svg-icon > :first-child {
  transition: all 0.3s;
  transform: translateX(-150%);
}
.more-btn .svg-icon > :last-child {
  transition: all 0.3s;
  transform: translateX(-50%);
}
.more-btn::after {
  content: "";
  position: absolute;
  display: inline;
  width: 0%;
  height: 1px;
  left: 0;
  bottom: -2px;
  opacity: 1;
  transition: all 0.3s;
